// 回到顶部插件
.cd-top{
	position:fixed;
	right:20px;
	bottom:60px;
	z-index:1000;
	height:215px;
	ul{
		li{
			font-size: 14px;
		    border: 1px solid #d7d7d7;
		    background-color: #fff;
		    // display: none;
		    width: 40px;
		    height: 40px;
		    color: #999;
		    margin-bottom: 3px;
		    text-align: center;
		    position:relative;
		    cursor:pointer;
		    &:hover{
		    	background:$blue;
		    	a,p,i{
		    		color:$white;
		    	}
		    	.scroll-top-nav p{
		    		color: #3c3a42;
		    	}
		    }
		    a{
		    	display: block;
		    }
		    .scroll-top-nav{
		    	padding-top:10px;
		    	width:120px;
		    	height: 140px;
		    	background:$white;
		    	position:absolute;
		    	left: -130px;
		    	display: none;
		    	border:1px solid #d7d7d7;
		    	cursor:default;
		    	&:after{
		    		content: '';
				    display: block;
				    background: url(../images/scroll_top_sj.png) no-repeat ;
				    width: 7px;
				    height: 10px;
				    position: absolute;
				    // top: 25px;
				    right: -6px;
		    	}
		    	img{
		    		width:100px;
		    		height: 100px;
		    		display: block;
		    		margin:0 auto;
		    	}
		    	p{
		    		font-size: 14px;
		    		line-height: 20px;

		    	}
		    }
			.gmzx{
				width:180px;
				left:-190px;
				p{
					font-size: 18px!important;
					text-align: left;
    				padding-left: 18px;
    				padding-bottom:15px;
    				margin-bottom: 15px;
    				border-bottom:1px solid #eee;
    				span{
    					display: block;
    					margin-top: 4px;
    				}

				}
				a{
					display: block;
					width:140px;
					padding:7px 0;
					text-align: center;
					background: $blue;
					margin:0 auto;
					border-radius:4px;
					img{
						width:18px;
						height: auto;
						display: inline-block;
						position: relative;
					}
					span{
						color:$white;
					}
				}
			}
		    .gmzx,.weibo,.weixin{
				top:0;
				&:after{
		    		top:15px;
		    	}
		    }
		    .app{
		    	top:-100px;
		    	&:after{
		    		bottom:14px;
		    	}
		    }
		}
		li:nth-child(2),li:nth-child(3),
		li:nth-child(1),li:nth-child(4){
			i{
				font-size: 24px;
			}
		}
		
		li:nth-child(5){
			display: none;
			i{
				font-size: 23px;
			}
		}
	}
}

// 插件web_loading
.spinner {
  margin: 100px auto 0;
  width: 40px;
  height: 40px;
  position: relative;
}
 
.container1 > div, .container2 > div, .container3 > div {
  width: 12px;
  height: 12px;
  background-color: $blue;
 
  border-radius: 100%;
  position: absolute;
  // -webkit-animation: bouncedelay 1.2s infinite ease-in-out;
  animation: bouncedelay 1.2s infinite ease-in-out;
  // -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
 
.spinner .spinner-container {
  position: absolute;
  width: 100%;
  height: 100%;
}
 
.container2 {
  // -webkit-transform: rotateZ(45deg);
  transform: rotateZ(45deg);
}
 
.container3 {
  // -webkit-transform: rotateZ(90deg);
  transform: rotateZ(90deg);
}
 
.circle1 { top: 0; left: 0; }
.circle2 { top: 0; right: 0; }
.circle3 { right: 0; bottom: 0; }
.circle4 { left: 0; bottom: 0; }
 
.container2 .circle1 {
  // -webkit-animation-delay: -1.1s;
  animation-delay: -1.1s;
}
 
.container3 .circle1 {
  // -webkit-animation-delay: -1.0s;
  animation-delay: -1.0s;
}
 
.container1 .circle2 {
  // -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}
 
.container2 .circle2 {
  // -webkit-animation-delay: -0.8s;
  animation-delay: -0.8s;
}
 
.container3 .circle2 {
  // -webkit-animation-delay: -0.7s;
  animation-delay: -0.7s;
}
 
.container1 .circle3 {
  // -webkit-animation-delay: -0.6s;
  animation-delay: -0.6s;
}
 
.container2 .circle3 {
  // -webkit-animation-delay: -0.5s;
  animation-delay: -0.5s;
}
 
.container3 .circle3 {
  // -webkit-animation-delay: -0.4s;
  animation-delay: -0.4s;
}
 
.container1 .circle4 {
  // -webkit-animation-delay: -0.3s;
  animation-delay: -0.3s;
}
 
.container2 .circle4 {
  // -webkit-animation-delay: -0.2s;
  animation-delay: -0.2s;
}
 
.container3 .circle4 {
  // -webkit-animation-delay: -0.1s;
  animation-delay: -0.1s;
}
 
// @-webkit-keyframes bouncedelay {
//   0%, 80%, 100% { -webkit-transform: scale(0.0) }
//   40% { -webkit-transform: scale(1.0) }
// }
 
@keyframes bouncedelay {
  0%, 80%, 100% {
    transform: scale(0.0);
    // -webkit-transform: scale(0.0);
  } 40% {
    transform: scale(1.0);
    // -webkit-transform: scale(1.0);
  }
}

.img-qq{
	animation: zxgif 1s infinite;
	// animation-iteration-count: infinite;
}
@keyframes zxgif {
	0%   {opacity: 1;}
	25%  {opacity: 0.4;}
	50%  {opacity: 1;}
	75%  {opacity: 0.4;}
	100%  {opacity: 1;}
}