@import '_variable.scss';
/* 尺寸大于等于768px，小于992px，ipad端 */
@media screen and (max-width: 991px) {
  * {
    font-size: 14px;
  }
  header .sy-nav li a {
    font-size: 14px;
  }
  // 首页
  .footer ul li {
    .lxfs {
      width: 90%;
      p {
        line-height: 27px;
      }
    }
  }
  .content .title {
    font-size: 27px;
    margin-top: 16px;
  }
  .content1 .title span {
    font-size: 27px;
  }
  .content1 .description {
    font-size: 16px;
    letter-spacing: 15px;
  }
  .application-market {
    margin: 10px auto;
    li p {
      font-size: 12px;
    }
  }
  .content3 {
    h3 {
      font-size: 26px;
      line-height: 80px;
    }
    a {
      border-radius: 25px;
      margin-top: 20px;
      font-size: 14px;
      width: 250px;
      height: 50px;
      line-height: 50px;
      padding-left: 40px;
      i {
        display: inline-block;
        width: 60px;
        height: 50px;
        background-size: 64% 28%;
      }
    }
    p {
      font-size: 14px;
    }
  }
  .content4 {
    .swiper-slide{
      width:32%;
      margin:0 10px;
    }
    .article-more {
      line-height: 40px;
    }
    ul {
      li {
        padding: 0 5px;
        margin-bottom: 10px;
      }
      .content4-list {
        padding-bottom: 20px;
        h4 {
          padding: 0 10px;
          line-height: 25px;
          font-size: 16px;
          margin: 0;
        }
        p {
          padding: 0px 10px;
          line-height: 22px;
          font-size: 14px;
          margin-bottom: 0;
        }
      }
    }
  }

  // 首页 end

  .content-post {
    padding: 15px 20px;
    background-color: #fff;
  }

  .data-nav {
    ul {
      // padding: 20px 20px;
      li {
        font-size: 14px;
      }
    }
  }
  .data-list li {
    .data-list-div {
      div, span, p {
        font-size: 14px;
      }
      .app-name {
        font-size: 16px;
        line-height: 16px;
      }
      .app-num {
        line-height: 60px;
      }
      img {
        margin-left: 0;
      }
      .orange-index {
        width: 57px;
        height: 57px;
        background: url('../images/cz_slevel.png') no-repeat center left;
        text-align: center;
        line-height: 57px;
        color: #fff;
        font-size: 14px;
      }
      .download-quantity,
      .star-rating {
        display: block;
        padding-left: 15px;
        font-size: 12px !important;
      }
      .download-quantity {
        line-height: 20px;
      }
      .star-rating {
        margin-top: -5px;
        img {
          width: 12px;
          height: 12px;
          margin-right: -2px;
        }
      }
    }
  }
  .legend {
    top: 0px;
  }
  .our-advantage {
    h4 {
      font-size: 16px;
    }
    p {
      font-size: 14px;
    }
  }

  .orange-information li {
    .orange-article {
      .date {
        p:nth-child(1) {
          line-height: 70px;
          font-size: 30px;
        }
        p:nth-child(2) {
          font-size: 15px;
          line-height: 35px;
        }
      }
      .art-content {
        h3 {
          font-size: 18px;
        }
        p {
          font-size: 13px;
        }
        a {
          padding: 4px 8px;
        }
      }
    }
  }

  .article-recommend ul {
    h4 {
      width: 150px;
      font-size: 14px;
      line-height: 40px;
    }
    li {
      width: 150px;
      line-height: 36px;
      a {
        font-size: 14px;
      }
    }
  }

  .footer {
    padding: 10px 10px;
  }

  .product-lunbo .featured-post .filter {
    display: none;
  }
  .app-bg-top {
    top: 15px;
    left: 15px;
  }
  .report-content {
    margin-top: 0;
    .app-bg-right {
      top: 15px;
      right: 15px;
    }
    .app-bg-bottom {
      bottom: 15px;
      right: 15px;
    }
    .app-bg-left {
      bottom: 15px;
      left: 15px;
    }
    .report-content-all {
      padding: 30px 30px;
      .left {
        h4 {
          font-size: 16px;
          margin: 0;
        }
        p {
          font-size: 12px;
          margin: 10px auto 0;
          line-height: 14px;
        }
      }
      .middle {
        padding: 0px 20px;
        min-height: 160px;
        p {
          font-size: 12px;
          line-height: 25px;
          span {
            // line-height: 10px;
            font-size: 15px;
          }
        }
        .zhishu-tit {
          width: 57px;
          text-align: center;
        }
        .zhishu {
          width: 57px;
          height: 57px;
          background: url(../images/cz_level.png) center left no-repeat;
          line-height: 57px;
        }
      }
      .right {
        .radar-map {
          width: 220px;
          height: 150px;
          margin: 0 auto;
        }
      }
    }
  }

  .basic-infor .app-message {
    line-height: 50px;
    font-size: 14px;
  }
  .basic-infor h4, .every-xm h4 {
    font-size: 16px;
  }
  .upload-title {
    font-size: 44px;
  }
  .upload-title span {
    font-size: 44px;
  }
  .btn-change, .btn-save {
    max-width: 130px;
    font-size: 14px;
    height: 34px;
    line-height: 34px;
  }
  .myModal-rigst {
    width: 90%;
    margin: 0 auto;
  }
  .myModal-rigst .modal-dialog {
    width: 690px;
  }
  .cz_about .main {
    p {
      line-height: 31px;
      font-size: 14px;
      text-indent: 2em;
    }
    .left p {
      text-indent: 0;
      margin-bottom: 0;
    }
  }
}