@import '_variable';
@import 'web_plug_in';
/* 总体样式风格设置 */
* {
  font-family: '微软雅黑';
  font-size: 16px;
  color: $main_ztys;
}

.main-body {
  overflow: hidden;
  position: relative;
  background-color: #f5f5f5;
  //background-image: url("https://www.transparenttextures.com/patterns/cubes.png");
}
.main-body,header{
  max-width: 1920px;
  margin:0 auto!important;
}

ul {
  margin: 0;
  padding: 0;
}

ul li {
  list-style: none;
}

input {
  outline: none;
}

input:-webkit-autofill,
button:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 1000px white inset;
}

#alert_zz {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .7);
  #self_alert {
    position: fixed;
    z-index: 9999;
    top: 200px;
    left: 50%;
    width: 500px;
    min-height: 200px;
    margin-left: -250px;
    padding: 20px;
    background: $white;
    h3 {
      font-size: 18px;
      line-height: 40px;
      margin-top: 0;
      text-align: center;
      border-bottom: 1px solid $border;
    }
    p {
      min-height: 70px;
      text-indent: 2em;
    }
    #alert_btn {
      line-height: 30px;
      display: inline-block;
      float: right;
      width: 100px;
      text-align: center;
      color: $white;
      border: none;
      border-radius: 4px;
      background: $blue;
    }
  }
}

.max-width {
  overflow: hidden;
  max-width: 1200px !important;
  margin: 0 auto;
}

.on {
  color: $blue;
}

.person-on1 {
  color: $white !important;
  background: $blue;
}

.person-on2 {
  color: $white !important;
  background: linear-gradient(top, #ff8a3e, #b64d09, #ff8a3e);
}

/* web_layout.html */
header {
  z-index: 10000;
  height: 80px;
  background: rgba(255, 255, 255, .9);
  box-shadow: 0 1px 1px $border;
  .logo {
    width: auto;
    max-width: 100%;
    height: auto;
    max-height: 100%;
    padding-top: 20px;
    padding-left: 15px;
  }
  .sy-nav li {
    font-size: 16px;
    line-height: 50px;
    float: left;
    width: 15%;
    height: 50px;
    margin-top: 20px;
    margin-left: 1%;
    text-align: center;
    a {
      display: block;
    }
  }
  .testing-app,
  .rigst-btn {
    display: inline-block;
    min-width: 80px;
    float: right;
    margin-top: 25px;
    margin-right: 10px;
    padding: 5px 9px;
    cursor: pointer;
    text-align: center;
    color: $white;
    border-radius: 6px;
    background: linear-gradient(top, #56acf6, #397df3);
    // background: linear-gradient(top,#0eaef1,#01588d);
    position: relative;
  }
  .person-msg {
    position: absolute;
    top: 32px;
    left: 0;
    display: none;
    ul li a {
      line-height: 30px;
      display: block;
      text-align: center;
      color: $white;
      background: linear-gradient(left, #56acf6, #397df3);
      margin-top: 15px;
      border-radius: 6px;
      min-width: 85px;
    }
  }
  #nav_btn {
    font-size: 32px;
    display: inline-block;
    float: right;
    margin-right: 20px;
    margin-left: 30px;
    padding-top: 18px;
    text-align: center;
    color: $blue;
  }
}

.phone-nav li
.head-nav2 {
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  display: none;
  background: rgba(255, 255, 255, .6);
}

.main-body:before {
  display: block;
  overflow: hidden;
  height: 80px;
  content: '';
}

.content {
  padding: 40px 0;
}

.footer {
  position: relative;
  z-index: 350;
  background: linear-gradient(top, #686565, #2c2e33);
  .lxfs {
    border-bottom: 1px solid #343333;
    .lxfs-div {
      padding: 22px 0;
      max-width: 1200px;
      margin: 0 auto;
    }
    span {
      display: inline-block;
      color: $white;
      width: 20%;
      text-align: center;
    }
    span + span {
      border-left: 1px solid #fff;
    }
    span:nth-child(5) {
      color: $orange;
      font-weight: bold;
      i {
        color: #fa842f;
        font-size: 20px;
      }
    }
  }
  ul {
    max-width: 1200px;
    margin: 50px auto 0;
    li {
      width: 20%;
      span {
        color: $white;
        display: block;
        font-size: 14px;
        margin-bottom: 10px;
        i {
          display: inline-block;
          width: 22px;
          height: 22px;
          margin-left: 12px;
          vertical-align: middle;
          background: url('../images/cz_icon01.png') 0 0 no-repeat;
        }
      }
      &:nth-child(1) i {
        background-position: -25px 0;
      }
      &:nth-child(2) i {
        background-position: 0 0;
      }
      &:nth-child(3) i {
        background-position: -54px 0;
      }
      &:nth-child(4) i {
        background-position: -82px 0;
      }
      &:nth-child(5) i {
        background-position: right 0;
      }
      img {
        width: auto;
        height: auto;
        max-width: 100%;
        max-height: 100%;
      }
      p {
        color: $white;
        font-size: 12px;
        text-align: center;
        margin: 10px 0 0
      }
    }
  }
}

.phone-color {
  animation: changeColor 0.6s infinite;
}

@keyframes changeColor {
  33% {
    color: #fa842f
  }
  66% {
    color: #c6a331
  }
  99% {
    color: #d4b49f
  }
}

.copyright {
  padding: 35px 10px;
  background: #2c2e33;
  z-index: 350;
  position: relative;
  p {
    margin-bottom: 0;
    text-align: center;
    color: #a4a6aa;
  }
}

/* web_layout.html end */

