  background: $blue;
}

.data-page {
  width: 100%;
  margin-top: 50px;
  text-align: center;
  &:before {
    display: block;
    overflow: hidden;
    height: 0;
    content: '';
  }
  .page-first,
  .page-last {
    line-height: 38px;
    display: inline-block;
    display: inline-block;
    height: 38px;
    padding-right: 10px;
    padding-left: 10px;
    border: 1px solid #ddd;
    i {
      color: $blue;
    }
    &:hover {
      color: $white;
