@import '_variable.scss';
/* 尺寸小于768px,手机端 */
@media screen and (max-width: 767px) {
  #alert_zz {
    #self_alert {
      width: 300px;
      min-height: 200px;
      top: 200px;
      left: 50%;
      margin-left: -150px;
      h3 {
        font-size: 16px;
        line-height: 30px;
      }
      #alert_btn {
        width: 100px;
        line-height: 30px;
      }
    }
  }
  .copyright {
    padding: 16px 10px;
    p {
      font-size: 12px;
      line-height: 20px;
    }
  }
  // web_layout
  header {
    #nav_btn {
      display: inline-block;
    }
    .sy-nav {
      display: none;
    }
  }
  // .footer ul li{
  // 	padding-right: 0;
  // 	// height: 120px;
  // 	margin-bottom: 10px;
  // 	span{
  // 		font-size: 12px;
  //     	margin-bottom: 5px;
  // 	}
  // 	div img{
  // 		width: auto;
  // 		height: auto;
  // 		max-width: 100%;
  // 		max-height: 100%;
  // 	}
  // 	p {
  // 	    font-size: 12px;
  // 	    line-height: 22px;
  // 	    padding-left: 8px;
  // 	    margin-bottom: 0;
  // 	}
  // }
  .footer .lxfs {
    border-bottom: 1px solid #787373;
    .lxfs-div {
      padding: 0 0 10px;
      span {
        width: 33%;
        font-size: 12px;
        border: none;
      }
      span:nth-child(4) {
        width: 50%;
      }
      span:nth-child(5) {
        width: 50%;
      }

    }
  }

  // index.html
  .content {
    padding: 8px 0 35px;
  }
  .content .title {
    font-size: 20px;
    letter-spacing: 5px;
    i {
      display: none;
    }
    span {
      font-size: 20px;
      letter-spacing: 5px;
    }
  }
  .content1 .description {
    font-size: 14px;
    letter-spacing: 5px;
  }
  .application-market {
    margin: 10px auto;
  }
  /* .application-market li img {
      max-width: 60%;
      max-height: 60%;
  } */
  .our-advantage p {
    font-size: 14px;
  }
  .content1 {
    padding-bottom: 0;
  }
  .content2 {
    padding-top: 0;
  }
  .content3 {
    h3 {
      text-align: center;
    }
    p {
      text-align: center;
    }
    a {
      margin: 0 auto;
    }
  }
  .content4 {
    .swiper-slide{
      width:48%;
      margin:0 10px;
      .content4-list{
        .content-hei{
          height: 100px;
          padding-top: 10px;
          bottom:28px;
        }
        img{
          margin-bottom: 116px;
        }
        h4{
          font-size:14px;
          padding: 0 10px;
          //height: 40px!important;
          line-height: 18px;
        }
        p{
          font-size:12px;
          padding: 0 10px;
          //height: 60px!important;
          line-height: 16px;
        }
      }
    }
  }
  .content4 ul .content4-list .first {
    font-size: 13px;
    line-height: 19px;
  }
  .orange-information {
    padding: 0;
  }
  //data页面
  .data-nav {
    display: none;
  }

  .data-list li {
    padding: 0 !important;
    .data-list-div {
      margin: 0 !important;
      .orange-number {
        font-size: 14px !important;
        margin-top: 0 !important;
      }
      div, span, p {
        font-size: 14px;
      }
      .app-name {
        font-size: 18px;
        line-height: 30px;
      }
      .download-quantity,
      .star-rating {
        display: inline-block;
        font-size: 12px !important;
      }
      .download-quantity {
        line-height: 20px;
        padding-left: 15px;
      }
      .star-rating {
        width: 82px !important;
        margin-top: -5px;
        padding-left: 10px;
        i{
          display:none;
        }
        img {
          width: 12px;
          height: 12px;
          margin-right: -2px;
        }
      }
    }
  }
  .data-page {
    .page-fh {
      display: none;
    }
    .tiaozhuan {
      display: block;
      text-align: center;
      width: 120px;
      margin: 20px auto 0;
    }
  }

  // 安全产品
  .pro-article-content table tr td {
    font-size: 14px;
  }
  .product-lunbo .slide-list {
    padding: 0;
    width: 50%;
  }
  .product article {
    .featured-post .filter {
      display: none;
    }
    .content-post .title-post {
      margin-top: 0;
    }
  }

  // 橙子资讯
  .news-nav {
    display: none;
  }
  .art-content h3 {
    margin-top: 10px;
    font-size: 16px !important;
  }
  .article-content .article-list li {
    .orange-article {
      .date {
        p:nth-child(1) {
          line-height: 60px;
          font-size: 30px;
        }
        p:nth-child(2) {
          font-size: 15px;
          line-height: 40px;
        }
      }
      .art-content {
        h3 {
          font-size: 18px;
          white-space:nowrap;
          overflow:hidden;
          text-overflow:ellipsis;
        }
        p {
          font-size: 13px;
          white-space:nowrap;
          overflow:hidden;
          text-overflow:ellipsis;
          margin-bottom: 7px;
        }
        a {
          padding: 4px 8px;
        }
      }
    }
  }

  // about us
  .cz_about .main {
    padding: 30px 15px;
    p {
      line-height: 33px;
      font-size: 14px;
    }
  }

  // 用户注册，登录
  .login-main-content {
    padding: 0;
  }

  // app_info
  .report-content {
    margin: 0 auto;
    border: #d9e3f3;
    .report-content-all {
      width: 100%;
      padding: 0px 30px;
      box-shadow: none;
      border: none;
      .left, .middle, .right {
        padding: 20px 0;
        border-bottom: 1px solid #d3e4ff;
      }
      .middle {
        border-left: none;
        border-right: none;
      }
      .left {
        h4 {
          font-size: 18px;
          line-height: 30px;
        }
        p {
          font-size: 14px;
          line-height: 12px;
        }
      }
      .right {
        .radar-map {
          width: 100%;
          height: 220px;
          margin: 0 auto;
        }
      }
      .app-title {
        font-size: 20px;
        color: #fa842f;
      }
    }
  }
  .every-xm {
    .table_infor {
      margin-left: 15px;
    }
    .danger, .suggest, .description {
      padding-left: 15px;
    }
  }

  .czaq-zhishu {
    font-size: 16px;
  }
  .czap-descript {
    margin-top: 0px;
    font-size: 14px;
  }
  .every-xm .table_infor tr td,
  .basic-infor table tr td {
    padding: 10px 0;
  }
  .every-xm .table_infor .dsfk-name {
    word-break: break-all;
  }
  .pro-article .title {
    h3 {
      font-size: 20px;
    }
    p span {
      width: 150px;
    }
  }
  .pro-article-content {
    h4 {
      font-size: 16px;
      line-height: 40px;
    }
    p {
      font-size: 14px;
      line-height: 28px;
    }
  }
  .fl-btn-box {
    background: linear-gradient(top, #e7f5ff, #bedce7);
  }
  .data-search1 {
    background: linear-gradient(top, #bedce7, #95c4d5);
  }
  .app-recommend {
    li {
      a {
        width: 169px;
        img {
          width: 60px;
          height: 60px;
          border-radius: 11px;
        }
        p {
          line-height: 40px;
        }
      }
    }
    .left, .right {
      padding: 0 !important;
      border-bottom: none !important;
    }
    .star-rating img {
      width: 14px;
      height: 14px;
      margin-right: -3px;
    }
  }

  .basic-infor p, .every-xm p {
    font-size: 14px;
  }
  #historical_change,
  #index_contrast_all,
  #index_contrast_same {
    width: 100%;
    min-height: 200px;
    margin: 0 auto;
  }
  .data-progress-val {
    margin: 0;
  }
  .legend {
    top: 7px;
  }
  .legend-btn {
    right: 12px;
    font-size: 12px;
    padding: 2px 6px;
  }

  //app_upload页面
  .upload-title {
    font-size: 30px;
    margin: 30px 0;
    span {
      font-size: 30px;
    }
  }
  .upload-body {
    padding: 50px 0;
    .left {
      padding: 0;
      p {
        border-right: none;
      }
    }
    .right {
      padding: 30px 0;
      .updown {
        height: 40px;
        input {
          height: 40px;
        }
        .sp01, .sp02 {
          line-height: 40px;
          font-size: 18px;
        }
      }
    }
  }
  #app_upload {
    font-size: 18px;
    line-height: 40px;
    border-radius: 20px;
  }
  .progress_bar .tbox {
    height: 14px;
    .tiao {
      height: 14px;
      font-size: 12px;
    }
  }
  .upload-btn a {
    font-size: 16px;
    line-height: 40px;
    border-radius: 20px;
    margin-bottom: 40px;
  }

  .person-content {
    .left {
      border: none;
    }
    .mobile-pad {
      padding: 0;
    }
    .right {
      border: none;
      .person-content-list {
        padding: 5px;
        min-height: 250px;
      }
      .person-content-list1 {
        ul {
          padding: 0 15px;
          li a {
            .app-name {
              line-height: 20px;
            }
            // .detail{
            // 	background: #fff;
            // 	color:$blue;
            // }
          }
        }
      }
      .person-content-list2 {
        .user-remind {
          margin: 16px 4px 12px;
        }
        ul li {
          label {
            width: 35%;
          }
          .text-input {
            width: 62%;
          }
          .user-code {
            margin-left: 35%;
            input, button {
              width: 48%;
            }
          }
          .phone-xxms {
            margin-left: 35%;
            font-size: 12px;
            line-height: 14px;
            margin-top: 5px;
          }
          .user-date-sbm {
            margin: 20px auto 0;
          }
        }
      }
      .person-content-list3 {
        .title {
          font-size: 12px;
          padding: 0;
          // text-align: left;
        }
        .ts-style {
          font-size: 12px;
          padding: 0;
        }
      }
    }
  }

  // 404页面
  .cont404 {
    img {
      width: 200px;
      height: 120px;
      margin: 40px auto;
    }
    p {
      margin-bottom: 10px;
    }
  }
}

@media screen and (max-width: 400px) {
  header .testing-app {
    padding: 5px 15px;
  }
  header #nav_btn {
    margin-right: 10px;
    margin-left: 0px;
  }
  #content4_ul {
    width: 260px;
    li {
      clear: both;
      width: 250px;
      margin-bottom: 10px;
    }
  }
  .content4 {
    h4, p {
      margin: 10px 0 !important;
      height: auto !important;
    }
  }
  .data-list li .data-list-div .app-name {
    line-height: 22px;
  }
  .article-content .article-list li {
    .orange-article {
      .date {
        clear: both;
        width: 100%;
        p:nth-child(1) {
          float: right;
          width: 50%;
          line-height: 70px;
          margin-bottom: 0px;
          font-size: 25px;
        }
        p:nth-child(2) {
          float: left;
          width: 50%;
          line-height: 70px;
          margin-bottom: 0px;
          font-size: 20px;
        }
      }
      .art-content {
        clear: both;
        width: 100%;
        padding: 0 25px 15px;
      }
    }
  }
  .data-list li .data-list-div {
    .download-quantity,
    .star-rating {
      display: block;
      font-size: 12px !important;
      padding-left: 15px;
    }
  }

  .start-sx {
    display: none;
  }
  .person-content-list2 {
    .title {
      padding: 0px !important;
    }
    .change-mobile, .change-email {
      width: 70px;
    }
  }
  .person-content-list3 .ts-style {
    line-height: 16px;
  }
  .btn-change, .btn-save {
    max-width: 128px;
  }
}